.gameboard {
  display: grid;
  place-items: center;
  grid-gap: 10px;
  margin: 0 auto;
  width: 100%;
}

.gameboard__announcement {
  height: 250px;
}

.gameboard__tiles-section {
  width: 80%;
  max-width: 500px;
}

.gameboard__tiles-container {
  padding-top: 100%;
  position: relative;
}

.gameboard__tiles {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  place-items: center;
  grid-gap: 10px;
}

.gameboard__scoreboard-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
  width: 100%;
}

@media only screen and (max-width: 600px) {
  .gameboard {
    grid-gap: 30px;
  }

  .gameboard__announcement {
    display: none;
  }
}
